import { DEFAULT_FORM } from './default.form';
import { MONEY_FORM } from './money.form';
import { LOGIN_FORM } from './login.form';
import { USERDETAILS_FORM } from './user-details.form';


// list forms here so we can access/load by ID
// also allows us to generate different configs in form object


export const FORMS_VALUES = (disabled = false) => ({
  [DEFAULT_FORM().id]: DEFAULT_FORM(disabled).template,
  [USERDETAILS_FORM().id]: USERDETAILS_FORM(disabled).template,
  [MONEY_FORM().id]: MONEY_FORM(disabled).template,
  [LOGIN_FORM().id]: LOGIN_FORM(disabled).template,
});

